import router from '@/router';

export default {
    name: 'userCategoryAdd',
    data() {
        return {
            ruleForm: {
                skuType: '',
                infoType: '',
                matchType: '',
                partskuName: '',
                feedback: '',
                limit: '',
                required:'',
                sort:''
            },
            rules: {
                skuType: [
                    { required: true, message: '' }
                ],
                infoType: [
                    { required: true, message: '' }
                ],
                matchType: [
                    { required: true, message: '' }
                ],
                partskuName: [
                    { required: true, message: '' }
                ],
                feedback: [
                    { required: true, message: '' }
                ],
                limit: [
                    { required: true, message: '' },
                    { type: 'integer', message: ''}
                ],
                required: [
                    { required: true, message: '' }
                ],
                sort: [
                    { required: true, message: '' },
                    { type: 'integer', message: ''}
                ]

            },
            scopeOptions: [{
                value: '0',
                label: ''
            }, {
                value: '1',
                label: ''
            }],
            infoTypeOptions: [{
                value: '0',
                label: ''
            }, {
                value: '1',
                label: ''
            }, {
                value: '2',
                label: 'JSON'
            }],
            matchTypeOptions: [{
                value: '0',
                label: ''
            }, {
                value: '1',
                label: 'Code'
            }],
            requiredOptions: [{
                value: '0',
                label: ''
            }, {
                value: '1',
                label: ''
            }],

        };
    },
    mounted() {
        this.$nextTick(() => {

        });
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    alert('submit!');
                    this.routeTo('catgPartSkuEdit');
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        // 
        routeTo(url) {
            router.push({
                name: url
            });
        }
    }

};



