import Vue from 'vue';
import Router from 'vue-router';

import Loading from '@/components/page/loading';
import NotFoundComponent from '@/components/page/notFoundComponent';
import Hello from '@/components/Hello';

import Main from '@/components/page/_MAIN';

import Login from '@/components/page/login';

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            name: 'loading',
            component: Loading
        },
        {
            path: '/login',
            name: 'login',
            component: Login
        },
        {
            //  - 
            path: '/userReview/:currentView',
            name: 'userReview',
            component: Main
        },
        {
            //  -  - 
            path: '/userCategoryList',
            name: 'userCategoryList',
            component: Main
        },
        {
            //  -  - 
            path: '/userCategoryAdd',
            name: 'userCategoryAdd',
            component: Main
        },
        {
            //  -  - 
            path: '/userCategoryDetail',
            name: 'userCategoryDetail',
            component: Main
        },
        {
            //  - 
            path: '/catgConfig',
            name: 'catgConfig',
            component: Main
        },{
            //  - 
            path: '/catgAdd',
            name: 'catgAdd',
            component: Main
        },{
            //  - 
            path: '/catgPartSkuEdit',
            name: 'catgPartSkuEdit',
            component: Main
        },{
            //  -  -  - 
            path: '/catgPartskuAdd',
            name: 'catgPartskuAdd',
            component: Main
        },
        {
            // 404
            path: '/hello',
            name: 'hello',
            component: Hello
        },
        {
            // 404
            path: '*',
            name: 'notFound',
            component: NotFoundComponent
        }
    ]
});



