import router from '@/router';

export default {
    name: 'catgConfig',
    data() {
        return {
            tableData: [{
                1: '',
                2: ',,',
                3: '2017-07-10 18:43:05',
                4: '2017-07-10 18:43:05',
                5: 1 // ''
            }, {
                1: '',
                2: ',,',
                3: '2017-07-10 18:43:05',
                4: '2017-07-10 18:43:05',
                5: 0 // ''
            }]
        };
    },
    mounted() {
        this.$nextTick(() => {

        });
    },
    methods: {
        handleFn(index, rows, keyword) {
            if(keyword == 'available') return; // 

            if(index == undefined && !rows){
                this.routeTo('catgAdd');
            }else{
                if(keyword == 'category'){
                    this.routeTo('catgAdd');
                }else{
                    this.routeTo('catgPartSkuEdit');
                }
            }

        },
        // 
        handleSizeChange(val) {
            console.log(` ${val} `);
        },
        // 
        handleCurrentChange(val) {
            console.log(`: ${val}`);
        },
        // 
        routeTo(url) {
            router.push({
                name: url
            });
        }
    }

};



