<template>
    <div v-title data-title="--">
        <div class="catgPartskuAdd p-30">
            <!-- breadcrumb -->
            <el-breadcrumb separator="/">
                <i class="iconfont icon-home fl m-r-10"></i>
                <el-breadcrumb-item :to="{ path: '/catgConfig' }"></el-breadcrumb-item>
                <el-breadcrumb-item :to="{ path: '/catgPartSkuEdit' }"></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
            <!-- form  -->
            <el-row>
                <el-col :span="20" :offset="2">
                    <div class="title text-center m-b-25"></div>

                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">

                        <el-form-item label="" prop="scopeof" required>
                            <el-input type="text" placeholder="codecode_" v-model="ruleForm.skuType"></el-input>
                        </el-form-item>

                        <el-form-item label="" prop="infoType" required>
                            <el-radio-group v-model="ruleForm.infoType">
                                <el-radio v-for="item in infoTypeOptions" :key="item.value" :label="item.label" :disabled="item.value == 2">{{item.label}}</el-radio>
                            </el-radio-group>
                        </el-form-item>

                        <el-form-item label="" prop="matchType" required>
                            <el-radio-group v-model="ruleForm.matchType">
                                <el-radio v-for="item in matchTypeOptions" :key="item.value" :label="item.label">{{item.label}}</el-radio>
                            </el-radio-group>
                        </el-form-item>

                        <el-form-item label="" prop="partskuName" required>
                             <el-input type="text" v-model="ruleForm.partskuName"></el-input>
                        </el-form-item>

                        <el-form-item label="" prop="feedback" required>
                             <el-input type="text" v-model="ruleForm.feedback"></el-input>
                        </el-form-item>

                        <el-form-item label="" prop="limit" required v-if="ruleForm.infoType">
                            <div v-if="ruleForm.infoType == ''">
                                <span class="m-r-10"></span><el-input class="limit-width" type="number" v-model.number="ruleForm.limit"></el-input><span class="m-l-10"></span>
                            </div>
                            <div v-else>
                                <span class="m-r-10"></span><el-input class="limit-width" type="number" v-model.number="ruleForm.limit"></el-input><span class="m-l-10"></span>
                            </div>
                        </el-form-item>

                        <el-form-item label="" prop="required" required>
                            <el-radio-group v-model="ruleForm.required">
                                <el-radio v-for="item in requiredOptions" :key="item.value" :label="item.label">{{item.label}}</el-radio>
                            </el-radio-group>
                        </el-form-item>

                        <el-form-item label="" prop="sort" required>
                             <el-input type="number" placeholder="" v-model.number="ruleForm.sort"></el-input>
                        </el-form-item>

                        <el-form-item class="text-center">
                            <el-row :gutter="20">
                                <el-col :span="4" :offset="6">
                                    <el-button class="input-width" type="primary" @click="submitForm('ruleForm')">  </el-button>
                                </el-col>
                                <el-col :span="4">
                                    <el-button class="input-width" @click="resetForm('ruleForm')"></el-button>
                                </el-col>
                                <el-col :span="4">
                                    <el-button class="input-width" onclick="window.history.go(-1);"></el-button>
                                </el-col>
                            </el-row>
                        </el-form-item>
                    </el-form>

                </el-col>
            </el-row>

        </div>
    </div>
</template>

<script>
import pageSet from '@/components/main/js/catgPartskuAdd';

export default {
    name: 'catgPartskuAdd',
    data: pageSet.data,
    mounted: pageSet.mounted,
    computed: pageSet.computed,
    methods: pageSet.methods
};
</script>

<style lang="scss" scoped>
@import "../../assets/css/config.scss";
.title {
    font-size: 24px;
    font-weight: bold;
}
.input-width{width: 100%;}
.limit-width{width: auto;}
</style>




