<template>
    <div v-title data-title="--">
        <div class="catgAdd p-30">
            <!-- breadcrumb -->
            <el-breadcrumb separator="/">
                <i class="iconfont icon-home fl m-r-10"></i>
                <el-breadcrumb-item :to="{ path: '/catgConfig' }"></el-breadcrumb-item>
                <el-breadcrumb-item></el-breadcrumb-item>
            </el-breadcrumb>
            <!-- form  -->
            <el-row>
                <el-col :span="14" :offset="4">
                    <div class="title m-b-25"></div>

                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="130px">

                        <el-row>
                            <el-col :span="8">
                                <el-form-item label="" prop="catgname" required>
                                    <el-input type="text" v-model="ruleForm.catgname"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="8">
                                <el-form-item label="" prop="catgcode" required>
                                    <el-input type="text" v-model="ruleForm.catgcode"></el-input>
                                </el-form-item>
                            </el-col>
                            <el-col :span="8">
                                <el-form-item label="" prop="queryCategory" required>
                                    <el-select v-model="ruleForm.queryCategory" placeholder="">
                                        <el-option label="" value="0"></el-option>
                                        <el-option label="" value="1"></el-option>
                                    </el-select>
                                </el-form-item>
                            </el-col>
                        </el-row>

                        <el-form-item label="" prop="queryCondition" required>
                            <el-input type="textarea" v-model="ruleForm.queryCondition"></el-input>
                        </el-form-item>

                        <el-form-item label="" prop="initiateState" required>
                            <el-radio-group v-model="ruleForm.initiateState">
                                <el-radio label="0"></el-radio>
                                <el-radio label="1"></el-radio>
                            </el-radio-group>
                        </el-form-item>

                        <el-form-item label="" prop="category_cmpros_add" required>
                            <el-row>
                                <el-col :span="12">
                                    <el-card class="m-10">
                                        <div slot="header" class="card-title">
                                            <span></span>
                                        </div>
                                        <draggable class="card-list list-group"
                                        @end="onEnd"
                                        v-model="ruleForm.category_cmpros"
                                        :options="dragOptions">
                                            <transition-group tag="ul" type="transition" name="flip-list" >
                                                <li class="list-group-item" v-for="row in ruleForm.category_cmpros" :key="row">
                                                    <el-tag type="gray">{{row.category_cmpro_index}}</el-tag> {{row.carmodel_pro_name}}
                                                </li>
                                            </transition-group>
                                        </draggable>
                                    </el-card>
                                </el-col>
                                <el-col :span="12">
                                    <el-card class="m-10">
                                        <div slot="header" class="card-title">
                                            <span class="fr f12"></span>
                                            <span></span>
                                        </div>
                                         <draggable element="ul" class="card-list"
                                        @end="onEnd"
                                        v-model="ruleForm.category_cmpros_add"
                                        :options="dragOptions">
                                            <transition-group class="list-group" type="transition" name="flip-list" tag="ul">
                                                <li class="list-group-item" v-for="row in ruleForm.category_cmpros_add" :key="row">
                                                    <el-tag type="gray">{{row.category_cmpro_index}}</el-tag>
                                                    {{row.carmodel_pro_name}}
                                                    <el-checkbox class="fr" v-model="row.category_cmpro_required" :true-label="1"  :false-label="0"></el-checkbox>
                                                </li>
                                            </transition-group>
                                        </draggable> 
                                    </el-card>
                                </el-col>
                            </el-row>
                        </el-form-item>

                        <div class="title m-b-25"></div>
                        <div v-for="(part, index) in ruleForm.parts" :key="index">
                            <el-row>

                                <el-col :span="7">
                                    <el-form-item :label="'' + index" :key="part.name" :prop="'parts.'+index + '.name'" :rules="{required: true, message: '', trigger: 'blur'}">
                                        <el-input v-model="part.name"></el-input>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="7">
                                    <el-form-item :label="'' + index" :key="part.code" :prop="'parts.'+index + '.code'" :rules="{required: true, message: '', trigger: 'blur'}">
                                        <el-input v-model="part.code"></el-input>
                                    </el-form-item>
                                </el-col>
                                <el-col :span="7">
                                    <el-form-item :label="'' + index" :key="part.serial" :prop="'parts.'+index + '.serial'" :rules="{required: true, message: '', trigger: 'blur'}">
                                        <el-input v-model="part.serial"></el-input>
                                    </el-form-item>
                                </el-col>

                                <el-col :span="3">
                                    <el-button class="m-l-10" icon="delete" @click.prevent="removePart(item)"></el-button>
                                </el-col>
                            </el-row>
                        </div>

                        <el-form-item>
                            <el-button icon="plus" @click.prevent="addPart"></el-button>
                        </el-form-item>

                        <el-form-item>
                            <el-button class="input-width" type="primary" @click="submitForm('ruleForm')">  </el-button>
                        </el-form-item>

                    </el-form>

                </el-col>
            </el-row>

        </div>
    </div>
</template>

<script>
import pageSet from '@/components/main/js/catgAdd';

export default {
    name: 'catgAdd',
    components: pageSet.components,
    data: pageSet.data,
    mounted: pageSet.mounted,
    computed: pageSet.computed,
    methods: pageSet.methods
};
</script>

<style lang="scss" scoped>
@import "../../assets/css/config.scss";
.input-width {
    width: 30%;
}

.title {
    font-weight: bold;
    font-size: 18px;
}

.card-title {
    font-weight: bold;
    font-size: 15px;
}

.card-list{
    max-height: 200px;
    overflow-y: auto;
}

.list-group {
    min-height: 200px;
}

.ghost {
    opacity: .5;
    background: $color-D;
}

.flip-list-move {
    transition: transform 0.5s;
}

.list-group-item {
    padding: 0 10px;
    cursor: move;
}

.list-group-item i {
    cursor: pointer;
}
</style>

<style lang="scss">
@import "../../assets/css/config.scss";
.catgAdd {
    .el-card__body,
    .el-card__header {
        padding: 5px 10px;
    }
}
</style>



