import router from '@/router';
import draggable from 'vuedraggable';
// category_cmpro_id: 121066838118136832,//id
// carmodel_pro_name: '',//
// category_cmpro_index: 1,//
// category_cmpro_required: 1//(0: 1: )

/**
 * 
 */
const category_cmpros = [{
    category_cmpro_id: 121066838118136832,
    carmodel_pro_name: ''
}, {
    category_cmpro_id: 121066838118136833,
    carmodel_pro_name: ''
}, {
    category_cmpro_id: 121066838118136834,
    carmodel_pro_name: ''
}, {
    category_cmpro_id: 121066838118136835,
    carmodel_pro_name: ''
}, {
    category_cmpro_id: 121066838118136836,
    carmodel_pro_name: ''
}];

const category_cmpros_res = category_cmpros.map((val, index) => {
    return {
        ...val,
        category_cmpro_index: index + 1,
        category_cmpro_required: 0
    };
});

export default {
    name: 'catgAdd',
    components: {
        draggable
    },
    data() {
        return {
            ruleForm: {
                catgname: '',                          //
                catgcode: '',                          //
                queryCategory: '',                     //
                queryCondition: '',                    //
                initiateState: '1',                    //
                category_cmpros: category_cmpros_res,  //
                category_cmpros_add: [],               //
                parts: [{                              //
                    name: '',
                    code: '',
                    serial: '',
                    key: Date.now()
                }]
            },
            // 
            rules: {
                catgname: [
                    { required: true, message: '' }
                ],
                catgcode: [
                    { required: true, message: '' }
                ],
                queryCategory: [
                    { required: true, message: '' }
                ],
                queryCondition: [
                    { required: true, message: '' }
                ],
                initiateState: [
                    { required: true, message: '' }
                ],
                category_cmpros_add: [
                    { required: true, message: '' }
                ]
            }
        };
    },
    computed: {
        dragOptions() {
            return {
                animation: 0,
                group: 'card-list',
                ghostClass: 'ghost'
            };
        }
    },
    mounted() {
        this.$nextTick(() => {

        });
    },
    methods: {
        onEnd() {
            function mapFn(d) {
                d.map((val, index) => {
                    val.category_cmpro_index = index + 1;
                    return val;
                });
            }
            mapFn(this.ruleForm.category_cmpros);
            mapFn(this.ruleForm.category_cmpros_add);
        },
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.routeTo('catgPartSkuEdit');
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        removePart(item) {
            if (this.ruleForm.part.length == 1) return;
            var index = this.ruleForm.part.indexOf(item);
            if (index !== -1) {
                this.ruleForm.part.splice(index, 1);
            }
        },
        addPart() {
            this.ruleForm.parts.push({
                name: '',
                code: '',
                serial: '',
                key: Date.now()
            });
        },

        // 
        routeTo(url) {
            router.push({
                name: url
            });
        }
    }

};



