// 
import * as types from '@/vuex/modules/state-types';
// import ENV from '@/assets/js/env';
// import Util from '@/assets/js/utility';

// 
const state = {
    pageLoading: false
};

// 
const getters = {
    [types.DONE_PAGE_LOADING]: state => {
        return state.pageLoading;
    }
};

// 
const mutations = {

};

// ajax
const actions = {
    // 
    [types.FETCH_PAGE_LOADING]({ commit }, parm) {
        state.pageLoading = parm;
    }
};

export default {
    state,
    getters,
    mutations,
    actions
};



