import router from '@/router';

export default {
    name: 'userCategoryDetail',
    data() {
        return {
            ruleForm: {
                company: '0',
                brand: '0',
                category: '0',
                part: ['', ''],
                price: '8888',
                dateStart: '2017-07-13T16:00:00.000Z',
                dateEnd: '2018-07-13T16:00:00.000Z',
                salesman: ''
            },
            rules: {
                company: [
                    { required: true, message: '' }
                ],
                brand: [
                    { required: true, message: '' }
                ],
                category: [
                    { required: true, message: '' }
                ],
                part: [
                    { type: 'array', required: true, message: '' }
                ],
                price: [
                    { required: true, message: ''},
                    { type: 'integer', message: ''}
                ],
                dateStart: [
                    { type: 'date', required: true, message: '' }
                ],
                dateEnd: [
                    { type: 'date', required: true, message: '' }
                ],
                salesman: [
                    { required: true, message: '' }
                ]
            },
            companyOptions: [{
                value: '0',
                label: ''
            }, {
                value: '1',
                label: ''
            }, {
                value: '2',
                label: ''
            }, {
                value: '3',
                label: ''
            }, {
                value: '4',
                label: ''
            }],
            brandOptions: [{
                value: '0',
                label: 'GEK'
            }, {
                value: '1',
                label: 'YOP'
            }],
            categoryOptions: [{
                value: '0',
                label: ''
            }, {
                value: '1',
                label: ''
            }],
            partOptions: [{
                value: '0',
                label: ''
            }, {
                value: '1',
                label: ''
            }, {
                value: '2',
                label: ''
            }, {
                value: '3',
                label: ''
            }, {
                value: '4',
                label: ''
            }, {
                value: '5',
                label: ''
            }]
        };
    },
    mounted() {
        this.$nextTick(() => {

        });
    },
    methods: {
        // 
        routeTo(url) {
            router.push({
                name: url
            });
        }
    }

};



