<template>
    <div>
        <!-- tabla -->
        <el-table :data="tableData" border class="m-t-25">
            <el-table-column prop="1" label=""></el-table-column>
            <el-table-column prop="2" label=""></el-table-column>
            <el-table-column prop="3" label=""></el-table-column>
            <el-table-column prop="4" label=""></el-table-column>
            <el-table-column prop="5" label=""></el-table-column>
            <el-table-column prop="6" label=""></el-table-column>
            <el-table-column prop="7" label="" width="80">
                <template scope="scope">
                    <el-button @click="handleFn(scope.$index, tableData)" type="text" size="small"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--  -->
        <el-pagination class="m-t-25 text-center" @size-change="handleSizeChange" @current-change="handleCurrentChange" current-page.sync="0" layout="sizes, prev, pager, next" :page-sizes="[20, 50, 100, 200, 400]" :page-size="20" :total="1000">
        </el-pagination>
    </div>
</template>

<script>
export default {
    name: 'reviewed',
    data() {
        return {
            tableData: [{
                1: '',
                2: '13570336092',
                3: 'TT',
                4: 'TATK',
                5: '',
                6: '2017-07-10 18:43:05'
            }, {
                1: '',
                2: '13570336092',
                3: 'TT',
                4: 'TATK',
                5: '',
                6: '2017-07-10 18:43:05'
            }]
        };
    },
    mounted() {
        this.$nextTick(() => {

        });
    },
    methods: {
        // 
        handleFn(index, rows) {
            this.$confirm('?', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'info'
            }).then(() => {
                rows.splice(index, 1);
                this.$notify.info({
                    title: '',
                    message: '',
                    duration: 1000
                });
            });
        },
        // 
        handleSizeChange(val) {
            console.log(` ${val} `);
        },
        // 
        handleCurrentChange(val) {
            console.log(`: ${val}`);
        }
    }
};
</script>

<style lang="scss" scoped>
@import "../../../assets/css/config.scss";
</style>



